import {React,useState,useEffect,useMemo} from 'react'
import $ from 'jquery'


const Cart_test = ({modal, addList}) => {
  const [add,setAdd] = useState(Array.from({length : 14},()=> 1));
  const [price,setPrice] = useState(Array.from({length : 15}));
  const [sum,setSum] = useState(0); //     0  
 
//     
    const hide =()=>{
        $('.b_total').css('display','none');
        $('.b_cart').css('opacity','1');
      }  

  //   

  const remove = ()=>{
      $('tr#list').remove();
      setSum(sum - sum);
      $('.b_cimg').remove();
      $('.b_textTotal').remove();


  }
 //   
 const selectRemove = (price) =>{
  let select = document.querySelector('#list');
  select.parentNode.removeChild(select);
    setSum(sum - price);
  
 }


//    
const sumConfirm = ()=>{

  if(!window.confirm('  '+ $("select[name=store] option:selected").text() +',    '+ sum +' ?')){
    alert(" ");
  }else if(!window.confirm == false){
    $('tr#list').remove();
    setSum(sum - sum);
    $('.total').remove()
    $('.b_cimg').remove();
    $('.b_textTotal').remove();
      alert(" ");
  }
}




//   
let arr = []; //prop  addList   
let add_ = [...add]; // useState   
let _price = [...price]; // 
let addListPrice = [];


for(let i =0; i < addList.length; i++){

let cost = addList[i].price;
addListPrice[i] = addList[i].price;

//    

const plus = ()=>{
  add_[i] +=1
  setAdd(add_);
  _price[i] = cost * add_[i];
  setPrice(_price); 
  setSum(sum + cost);
  

}  

//      
const minus = ()=>{
  if(add[i] < 1){
    alert('   1 !')
  }else{
      add_[i] -=1
  setAdd(add_);
  _price[i] = price[i] - cost;
  setPrice(_price);
  setSum(sum - cost);  
  }

}

     arr.push(
        <>
        <tr key={addList[i].id} id="list">
        <td>{i+1}</td>
        <td>
        <div className='b_cimgs'>
            <img src={addList[i].url} alt="001" style={{width:"150px"}} />
            <p key={addList[i].id}>{addList[i].title} </p>
        </div>
        </td>
        <td>
        <input type="button" value="-" onClick={minus}/> 
         <span> {add[i]} </span>
         <input type="button" value="+" onClick={plus} />
        </td>
        <td>{price[i]}</td>
        <td><button className='btn btn-primary' onClick={()=>selectRemove(addList[i].price)}></button> </td>
        </tr>


        </>
     )
    }

// addList      useEffect
useEffect(()=>{
  setPrice(addListPrice);  //    
  let addListP = 0;       //       
  for(let j=0;j <addList.length;j++){
    addListP += addList[j].price;

  }
  setSum(addListP);
  
},[addList]);


  return (
    <>
<div className='b_total overflow-scroll' style={{display : modal ? "block" : "none"}}>
<div className='d-flex justify-content-between align-item-center' >
        <h3 className='b_name m-3 '>   </h3>
        <p className='hide' onClick={hide}> X </p>
       </div>
        <table className='b_cartListBox'>
          <tr className='cartNames'>
            <td>No</td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
          {arr}
          </table>
          <form action="/" className='text-center pt-3'>
          <span className='text-center mx-5 sumsum'>  : {sum}</span>
            <select name="store" id="store" className='text-center mx-5' >
              <option value="default">----- -----</option>  
              <option value=" "> </option>
              <option value=" "> </option>
              <option value=" "> </option>
              <option value=" "> </option>
              <option value=""></option>
              <option value=""></option>
            </select>  
          </form>  
  
          <div className='d-flex justify-content-around mt-5'>
          <span className='btn btn-warning'onClick={remove} > </span>  
         
            <span className='btn btn-warning' onClick={sumConfirm}></span>
          </div> 
    </div>
    </>
  )
}

export default Cart_test