import React from 'react'
import styled from "styled-components"

const Foot = styled.div`
margin:0;
padding:0;
box-sizing: border-box;
background-color: rgba(250, 250, 100);
width: 100%;
height: 400px;
position: relative;
transform: translatY(-100%);
`
const In = styled.div`
width: 50%;
height: 400px;
margin: 0 auto;
text-align:center;
padding-top: 30px;
img{
    width: 500px;
    padding-bottom: 30px;
}
span{ 
    font-size: 150px;
    color: white;
    padding-top:-100px;
}
.fa-minus{
    transform:rotate(90deg);
    color: #fa6464;
}
select{
    width: 290px;
    height: 30px;
    border: 2px solid #fa6464;
    border-radius: 4px;
    margin-right: 5px;
}
button{
    width: 50px;
    height: 30px;
    border:none;
    border-radius: 4px;
    background-color: #fa6464;
    color: white;
    margin-left: 5px;
}
button:hover{
    color: rgba(250, 250, 100);
}
p:last-child{
    margin-top: 20px;
}
`
// const Select = styled.div`


// `

const Footer = () => {

  return (
    <>
        <Foot>
            <In>
                <img src="/image/Logo/Logo.png" alt=""></img>
                <p> <i className="fa-solid fa-minus"></i>  <i className="fa-solid fa-minus"></i>  <i className="fa-solid fa-minus"></i>  <i className="fa-solid fa-minus"></i> / <i className="fa-solid fa-minus"></i> </p>
                <p>   CHK 10 <i className="fa-solid fa-minus"></i>  1544-1234</p>
                <p> CHK() <i className="fa-solid fa-minus"></i>  123-45-67890 <i className="fa-solid fa-minus"></i>     CHK()</p>
             


                <div>
                    <select>
                        <option value="A">=======    =======</option>
                        <option value="B">CHK </option>
                        <option value="B">CHK </option>
                        <option value="C">CHK </option>
                        <option value="D">CHK </option>
                        <option value="F">CHK </option>
                    </select>  
                    <button type="submit">Go!</button>
                </div>
                   <p>copyright &#169; Cafe HK all right reserved.</p>
            </In>
            
        </Foot>
    </>
  )
}

export default Footer