import React,{useState,useEffect} from 'react'
import $ from 'jquery'

const JwCart = ({modal,setModal,addList}) => {


const [number,setNumber] = useState(Array.from({ length: 14 }, () => 1));
const [price,setPrice] = useState(Array.from({ length: 14 }, () => 1));
const [total, setTotal] = useState(0);  //  =0



  // 
  const goPayment =  () => {
    alert('.    .     ^_^');
  }

  //  
  const xmark = () => {
    if ($('#jw_Cart').is(":visible")) {
      $('#jw_Cart').hide();
    }
  }

  // 
  const allDel = () => {
    $('.h_pick').empty();
    $('.j_total').text(0);
    return;
  }


//    

let arr = [];  
let _number = [...number];  // 
let _price = [...price];   //
let addListPrice = []; // addList     

for(let i =0; i<addList.length;i++){
  
  let cost = addList[i].price;
  addListPrice[i] = addList[i].price * number[i];

  addListPrice.push(addList[i].price); //     


 // +   

 const onIncrease = () => {  

  _number[i] += 1;
  setNumber(_number);
  _price[i] = cost * _number[i];
  setPrice(_price); 
  setTotal(total + cost);
}


// -   

const onDecrease = () => {
  if (number[i] === 1) {   // 1
    alert('  1.    "" !');
    return;
  }
  _number[i] -= 1;
  setNumber(_number);
  _price[i] = cost * _number[i];
  setPrice(_price); 
  setTotal(total - cost);
}


// 
const del = (proNum, title) => {   //proNum , title 
  if(window.confirm("  ?") == true){    //
        const test = number[i.idd];
        const proPri = $('.j_total').text();
        const totalVal= parseInt(proPri)-(proNum*test);  
        $('.j_total').text(totalVal);
        
         const delPro = document.getElementById(title);
         delPro.remove();
         setTotal(total - _price[i]);
      }else{   //
          return false;
      }
}

  arr.push(
    <>
    <ul className="h_pick-pd" id={addList[i].title}>
            <li><img src={addList[i].url} alt="product" /></li>
            <li className='proTitle'>{addList[i].title}</li>
            <li className='pprice'>{price[i]} \</li>  
            <li>: <button className="uuu" onClick={() => onIncrease()}>+</button> {number[i]} <button onClick={() => onDecrease()}>-</button></li>
            <li><button className="h_cancle" onClick={() => del(addList[i].id, addList[i].title)} >   </button></li>
          </ul>
    </>
  )
}

// addList      useEffect
useEffect(()=>{
  setPrice(addListPrice); 
},[addList]);

useEffect(()=>{
  addListPrice.pop();
  let aa = 0;
  for (let i = 0; i < addListPrice.length; i++) {
    console.log("  ", addListPrice);
    aa += addListPrice[i];
    console.log(aa);
    setTotal(aa);
  }
},[addList]);


  return (
    <>
     <div id="jw_Cart" style={{ display: modal ? "flex" : "none" }}>
      <div className='h-cart'>
        <div className="h_box">
          <div className="h_box-top">
            <span> </span>
            <span className="h_xmark" onClick={xmark}><i class="fa-solid fa-xmark"></i></span>
          </div>
          <div className="h_box-in">
            <div className="h_pick">
                {arr}
            </div>

          </div>
          <div className="h_box-footer">
            <p> : <span className='j_total'>{total}</span> \</p>
            <button className="h_box-footer_bt" onClick={goPayment} style={{ backgroundColor: "#fa6464", marginRight: "1%" }}> </button>
            <button className="h_box-footer_bt" onClick={xmark} style={{ backgroundColor: "rgba(250, 250, 100)" }}> </button>
            <button className="h_box-footer_bt" onClick={allDel} style={{ backgroundColor: "rgba(250, 250, 100)" }}> </button>
          </div>
        </div>
      </div>
    </div>
    
    </>
  )
}
export default JwCart